body {
  box-sizing: border-box;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif;
  font-size: 14px;
  margin: 0;
  background-color: #fff; }

a {
  color: #2e5bff; }

*, *::before, *::after {
  box-sizing: inherit; }

b, strong {
