  border: 0;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif;
  font-size: 14px; }
  button:focus {
    outline: none; }

select {
  cursor: pointer; }

input, optgroup, select, textarea {
  width: 100%;
  border: 0; }
  input:focus, optgroup:focus, select:focus, textarea:focus {
    outline: none; }

textarea {
  resize: none;
  margin: 0;
  font-family: "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Arial, sans-serif; }

input[type="checkbox"],
input[type="radio"] {
  padding: 0;
  margin: 0; }
